:root {
    --color: rgba(0, 0, 0, 0.85);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
}

.app {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 80vw;
    height: 80vh;
    max-width: 350px;
    max-height: 600px;
    padding: 1rem;
    border: 3px solid var(--color);
    margin: auto;
    margin-top: 2rem;
    overflow: hidden;
}

h2 {
    color: var(--color);
}

.status {
    width: 100%;
    padding: 0.5rem 0 0;
    font-family: sans-serif;
    text-transform: uppercase;
}

form {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

select {
    margin-top: 2rem;
    padding: 1rem;
    font-size: 2rem;
    width: 100%;
    color: var(--color);
    border: none;
    border-right: 1rem solid transparent;
    outline: 3px solid var(--color);
}

option {
    color: gray;
}

input {
    margin-top: 1rem;
    margin-bottom: auto;
    align-self: flex-start;
    padding: 0.5rem;
    font-size: 1rem;
    width: 25%;
    letter-spacing: 3px;
    border: 2px solid var(--color);
    font-weight: 600;
}

.dressed {
    margin-top: 0;
    margin-bottom: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: relative;
    left: 0;
    transition: all 200ms ease;
    opacity: 1;
}

.dressed:nth-child(4) {
    transition-delay: 150ms;
}

.dressed:nth-child(5) {
    transition-delay: 300ms;
}

.dressed h3 {
    font-family: sans-serif;
    font-weight: 100;
}

.dressed input {
    margin-top: 0;
    margin-right: 5rem;
}

.last {
    margin-bottom: 2.5rem;
}

.btn {
    font-family: sans-serif;
    text-align: center;
    font-size: 2.5rem;
    padding: 0.5rem 0;
    cursor: pointer;
    width: 80%;
    margin-bottom: 5rem;
    background-color: var(--color);
    color: white;
    border: none;
}

.hidden {
    left: -100px;
    opacity: 0;
}

.start-btn {
    background-color: darkgreen;
}

.stop-btn {
    background-color: darkred;
}

.save-btn {
    background-color: darkblue;
}